<template>
<header class="header">
  <div class="container">
    <div class="header__body">
      <div class="header__logo"><router-link :to=" { name: 'Home' } ">Logo</router-link></div>
      <button class="header__burger" @click="headerUpdate ()" v-bind:class="{ active: headerState }"><span></span></button>
      <nav class="header__menu" v-bind:class="{ active: headerState }">
        <ul class="header__list">
          <li class="header__link">
            <router-link :to=" { name: 'Donate' } ">Пожертвовать</router-link>
          </li>
          <li class="header__link">
            <router-link :to=" { name: 'Business' } ">Бизнесу</router-link>
          </li>
          <li class="header__link">
            <router-link :to=" { name: 'About' } ">О нас</router-link>
          </li>
          <li>
            <button class="button">
              Войти
            </button>
          </li>
          <li>
            <button class="button">
              Зарегистрироваться
            </button>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</header>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  name: "AppHeader",
  computed: {
    ...mapGetters([
      'headerState'
    ])
  },
  methods: {
    headerUpdate () {
      this.toggleHeaderState()
    },
    ...mapActions(['toggleHeaderState'])
  }
}
</script>

<style scoped lang="scss">
// Const
$color-1: #FF4242;
$color-2: #000;
$color-3: #fff;


.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 23;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $color-3;
    z-index: 21;
  }
  &__body {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 40px;
  }
  &__logo {
    z-index: 22;
    flex: 0 0 120px;
    max-width: 120px;
    overflow: hidden;
    img {
      max-width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
      object-position: center;
    }
  }
  &__list {
    display: flex;
    position: relative;
    z-index: 22;
    align-items: center;
    padding: 12px 0;
    @media (max-width: 997.98px) {
      flex-direction: column;
    }
    li {
      margin-right: 3rem;
      &:last-child {
        margin-right: 0;
      }
      a {
        position: relative;
        transition: .1s linear all;
        @media (max-width: 997.98px) {
          font-size: 2.4rem;
        }
        &:after {
          content: '';
          position: absolute;
          bottom: -4px;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: $color-1;
          transition: .1s linear all;
          transform: scale(0);
        }
        &:hover {
          color: $color-1;
          &:after {
            transform: scale(1);
          }
        }
        &:active {
          opacity: .6;
        }
      }
      button {
        @media (max-width: 997.98px) {
          font-size: 2.4rem;
        }
      }
      @media (max-width: 997.98px) {
        margin-right: 0;
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &__link a, &__logo a {
    color: $color-2;
    font-size: 1.8rem;
    font-weight: 600;
  }
  &__burger {
    display: none;
    @media (max-width: 997.98px) {
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
      border: none;
      background-color: transparent;
      z-index: 22;
      span {
        overflow: visible;
        position: absolute;
        width: 100%;
        height: 2px;
        top: 50%;
        left: 0;
        transform: translateY(-50%) scale(1);
        background-color: $color-2;
        opacity: 1;
        transition: .2s linear all;
      }
      &:before, &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        background-color: $color-2;
        transition: .2s linear all;
      }
      &:before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
      &.active {
        span {
          transform: translateY(-50%) scale(0);
          opacity: 0;
        }
        &:before {
          top: 50%;
          transform: rotate(-45deg) translateY(-50%);
        }
        &:after {
          bottom: 50%;
          transform: rotate(45deg) translateY(50%);
        }
      }
    }
  }
  &__menu {
    @media (max-width: 997.98px) {
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      position: fixed;
      background-color: $color-3;
      padding: 100px 20px 30px 20px;
      box-sizing: border-box;
      overflow: auto;
      opacity: .8;
      transition: .2s linear all;
      &.active {
        opacity: 1;
        left: 0;
      }
    }
  }
}
</style>
